<template>
  <div class="components-container">
    <div class="container">
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="first">
          <h3 class="title"></h3>
          <!-- - -->
          <el-form class="basic-form" ref="basicForm" :model="basicForm" :rules="rules" label-width="100px">
            <el-form-item label="" prop="name">
              <el-input v-model="basicForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="type">
              <el-select v-model="basicForm.type" placeholder="">
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item class="sys-mark-wrapper" label="" prop="sysMark">
              <el-input class="input-one" v-model="basicForm.sysMark"></el-input>
              <el-select class="input-two" v-model="basicForm.sysMarkType" placeholder="" @change="handleSysMarkType">
                <el-option label="" value="1" checked></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="accountMark">
              <el-input v-model="basicForm.accountMark"></el-input>
            </el-form-item>
            <el-form-item label="" required>
              <el-col :span="11">
                <el-form-item prop="region1">
                  <el-select v-model="basicForm.region1" placeholder="" @change="chooseProvince">
                    <el-option v-for="item of basicForm.region1List" :label="item.label" :key="item.index" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col class="line" :span="1">&nbsp;-</el-col>
              <el-col :span="11">
                <el-form-item prop="region2">
                  <el-select v-model="basicForm.region2" placeholder="" @change="chooseCity">
                    <el-option v-for="item of basicForm.region2List" :label="item.label" :key="item.index" :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-form-item>
            <h3 class="title"></h3>
            <el-form-item label="" prop="concat">
              <el-input v-model="basicForm.concat" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="/" prop="depart">
              <el-input v-model="basicForm.depart" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="tel">
              <el-input v-model="basicForm.tel" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="basicForm.email" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="remarks">
              <el-input type="textarea" v-model="basicForm.remarks"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitbasicForm('basicForm')"></el-button>
              <el-button @click="cancel"></el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="second">
          <!--  -->
          <el-col :span="6">
            <el-tree :data="departData" :props="defaultProps"></el-tree>
          </el-col>
          <el-col :span="1">
          </el-col>
          <!--  -->
          <el-col :span="16">
            <div>
              <el-select v-model="departType" placeholder="">
                <el-option
                  v-for="item in departOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
              <el-input v-model="content" width="auto" placeholder=""></el-input>
            </div>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { provinceAndCityData } from 'element-china-area-data'
export default {
  data() {
    return {
      activeName: 'first',
      basicForm: {
        name: '',
        type: '',
        sysMark: 'xxx.tencent.com',
        sysMarkType: '1',
        accountMark: '',
        region1: '',
        region1List: [],
        region2: '',
        region2List: [],
        concat: '',
        depart: '',
        tel: '',
        email: '',
        remarks: ''
      },
      departData: [{
        label: ' 1',
        children: [{
          label: ' 1-1',
          children: [{
            label: ' 1-1-1'
          }]
        }]
      }, {
        label: ' 2',
        children: [{
          label: ' 2-1',
          children: [{
            label: ' 2-1-1'
          }]
        }, {
          label: ' 2-2',
          children: [{
            label: ' 2-2-1'
          }]
        }]
      }, {
        label: ' 3',
        children: [{
          label: ' 3-1',
          children: [{
            label: ' 3-1-1'
          }]
        }, {
          label: ' 3-2',
          children: [{
            label: ' 3-2-1'
          }]
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      departType: '',
      departOptions: [{
        label: '',
        value: 1
      }],
      content: '',
      // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        sysMark: [
          { required: true, message: '', trigger: 'blur' }
        ],
        accountMark: [
          { required: true, message: '', trigger: 'blur' }
        ],
        region1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        region2: [
          { required: true, message: '', trigger: 'blur' }
        ],
        concat: [
          { max: 20, message: '20 ', trigger: 'blur' }
        ],
        depart: [
          { max: 20, message: '20 ', trigger: 'blur' }
        ],
        remarks: [
          { max: 200, message: '200 ', trigger: 'blur' }
        ]
      }
    }
  },
  // computed: {
  //   sysMark() {
  //     return this.basicForm.sysMark
  //   }
  // },
  watch: {
    'basicForm.sysMark': function(newVal, oldVal) {
      if(this.basicForm.sysMarkType === '2') {
        this.basicForm.accountMark = newVal
      }
    }
  },
  methods: {
    // 
    handleSysMarkType(val) {
      // -
      if(val === '1') {
        this.basicForm.sysMark = '.tencent.com'
      } else if(val === '2') { // -
        this.basicForm.sysMark = ''
      }
    },
    // -
    chooseProvince(val) {
      this.basicForm.region1 = val
      this.basicForm.region2 = ''
      let _provinceList = this.basicForm.region1List
      for(let provinceItem of _provinceList) {
        if(val === provinceItem.value) {
          this.basicForm.region2List = provinceItem.children
          break
        }
      }
    },
    // -
    chooseCity(val) {
      this.basicForm.region2 = val
    },
    // 
    submitbasicForm(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid) {
          let params = {
            province: this.basicForm.region1,
            city: this.basicForm.region2,
            customer_name: this.basicForm.name,
            customer_type: this.basicForm.type,
            sys_sign: this.basicForm.sysMark,
            sign_type: this.basicForm.sysMarkType,
            account_sign: this.basicForm.accountMark,
            contact: this.basicForm.concat,
            dept: this.basicForm.depart,
            phone: this.basicForm.tel,
            email: this.basicForm.email,
            remark: this.basicForm.remarks
          }
          console.log(params)
          // addOrg(_data).then( res => {
          //   console.log(res)
          // })
        } else {
          return false
        }
      })
    },
    // 
    cancel() {
      this.$refs['basicForm'].resetFields()
    },
    // tab
    handleClick(tab, e) {
      if(tab.name === 'second') {
        // nothing
      }
    }
  },
  created() {
    this.basicForm.region1List = provinceAndCityData
  }

}
</script>
<style scoped lang="stylus">
.basic-form
  width 500px
.sys-mark-wrapper
  .input-two
    position absolute
    top 0
    right -110px
    width 100px
</style>



